<template>
  <div>
    <el-card class="box-card">
      <h2>注册</h2>
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-position="left"
        label-width="80px"
        class="register-from"
      >
        <el-form-item label="账号" prop="uname">
          <el-input v-model="ruleForm.uname"></el-input>
        </el-form-item>

        <el-form-item label="密码" prop="pass">
          <el-input
            type="password"
            v-model="ruleForm.pass"
            autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="确认密码" prop="password">
          <el-input
            type="password"
            v-model="ruleForm.password"
            autocomplete="off"></el-input>
        </el-form-item>
      </el-form>

      <div class="btnGroup">
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
        <el-button @click="goback">返回</el-button>
      </div>

    </el-card>
  </div>
</template>

<script>
export default {
  data() {
      var validatePass = (rule, value, callback) => {
        if (value === "") {
          callback(new Error("请输入密码"));
        } else {
          if (this.ruleForm.checkPass !== "") {
            this.$refs.ruleForm.validateField("checkPass");
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === "") {
          callback(new Error("请再次输入密码"));
        } else if (value !== this.ruleForm.pass) {
          callback(new Error("两次输入密码不一致！"));
        } else {
          callback();
        }
      };

      return {
        ruleForm: {
          uname: "",
          pass: "",
          password: "",
        },

        rules: {
          uname: [{required: true, message:"账号不能为空!", trigger: "blur"},],
          pass: [{required: true, validator: validatePass, trigger: "blur"},],
          password: [{required: true, validator: validatePass2, trigger: "blur"}],
        },
      };
    },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        this.loading = true;  // 提交按钮显示加载动画
        if (valid) {
          let _this = this;
          this.axios({   // axios向后端发起请求
            url: "/api/user/register",    // 请求地址
            method: "post",               // 请求方法
            headers: {                    // 请求头
              "Content-Type": "application/json",
            },
            data: {    // 请求参数
              uname: this.ruleForm.uname,
              password: _this.ruleForm.password,
            },
          }).then((res) => {
            if (res.data.code === "0") {     // 响应的编码为 0 时，注册成功
              // 显示后端响应成功的信息
              this.$message({
                message: res.data.message,
                type: "success",
              });
            } else {     // 编码不为 0 ，注册失败
              // 显示后端响应失败的信息
              this.$message({
                message: res.data.message,
                type: "warning",
              });
            }
            // 收到后端响应后，不再显示登录按钮加载动画
            _this.loading = false;
            console.log(res);
          });
        } else {  // 账号或密码有一个没填写，则直接提示，不发送请求
          console.log("error submit!");
          this.loading = false;
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    goback() {
      this.$router.go(-1);
    },
  },
};
</script>

<style scoped>
.box-card {
  margin: auto auto;
  width: 400px;
}
.register-from {
  margin: auto auto;
}
</style>