<template>
  <div id="app">
    <div class="content-header">
      <h1>小说管理</h1>
    </div>

    <div class="app-container">
      <div class="box">
        <div class="filter-container">
          <el-input
              placeholder="书名"
              v-model="filters.title"
              style="width: 200px"
              class="filter-item"
              @keyup.enter="getAll"
          />

          <el-input
              placeholder="作者"
              v-model="filters.author"
              style="width: 200px"
              class="filter-item"
              @keyup.enter="getAll"
          />

          <el-input
              placeholder="类别"
              v-model="filters.type"
              style="width: 200px"
              class="filter-item"
              @keyup.enter="getAll"
          />

          <el-button @click="getAll" class="searchBut">查询</el-button>
          <el-button @click="resetInput" class="butT">重置</el-button>
          <el-button @click="addBook" class="butT" type="primary" plain>新建</el-button>
        </div>

        <el-table
            size="small"
            current-row-key="id"
            :data="dataList"
            stripe
            highlight-current-row
        >
          <el-table-column type="index" align="center" label="编号" />
          <el-table-column prop="title" label="书名" align="center" />
          <el-table-column prop="author" label="作者" align="center" />
          <el-table-column prop="type" label="类型" align="center" />
          <el-table-column prop="description" label="描述" align="center" :show-overflow-tooltip="true" />
          <el-table-column label="操作" align="center">
            <template #default="scope">
              <el-button type="primary" size="mini" @click="updateBook(scope.row)">
                编辑
              </el-button>
              <el-button type="danger" size="mini" @click="deleteBook(scope.row)">
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>

        <!-- 新增/编辑对话框 -->
        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible">
          <el-form
              ref="dataForm"
              :model="formData"
              :rules="rules"
              label-position="right"
              label-width="100px"
          >
            <el-row>
              <el-col :span="12">
                <el-form-item label="书名" prop="title">
                  <el-input v-model="formData.title" />
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="作者" prop="author">
                  <el-input v-model="formData.author" />
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="类别" prop="type">
                  <el-input v-model="formData.type" />
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="24">
                <el-form-item label="描述">
                  <el-input v-model="formData.description" type="textarea" />
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>

          <div slot="footer" class="dialog-footer">
            <el-button @click="handleCancel">取消</el-button>
            <el-button @click="submitForm">确定</el-button>
          </div>
        </el-dialog>

        <div class="pagination-container">
          <el-pagination
              :background="true"
              :small="true"
              @current-change="pageChange"
              @size-change="sizeChange"
              :page-sizes="[10, 20, 50]"
              :current-page="pagination.currentPage"
              :page-size="pagination.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="pagination.totalElements"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dataList: [],
      filters: {
        title: '',
        author: '',
        type: ''
      },
      pagination: {
        currentPage: 1,
        pageSize: 10,
        totalElements: 0
      },
      dialogVisible: false,
      isEdit: false,
      formData: {
        id: null,
        title: '',
        author: '',
        type: '',
        description: ''
      },
      rules: {
        title: [{ required: true, message: '书名不能为空', trigger: 'blur' }],
        author: [{ required: true, message: '作者不能为空', trigger: 'blur' }],
        type: [{ required: true, message: '类型不能为空', trigger: 'blur' }]
      }
    }
  },

  computed: {
    dialogTitle() {
      return this.isEdit ? '编辑小说' : '新增小说'
    }
  },

  mounted() {
    this.getAll()
  },

  methods: {
    async getAll() {
      try {
        const response = await this.axios.get('api/user/books', {
          params: {
            page: this.pagination.currentPage - 1,
            size: this.pagination.pageSize,
            title: this.filters.title,
            sort: 'id,asc',
            author: this.filters.author,
            type: this.filters.type
          }
        })
        this.dataList = response.data.content
        this.pagination.totalElements = response.data.totalElements
      } catch (error) {
        console.error('加载数据失败:', error)
      }
    },

    // 切换页数
    pageChange(page) {
      this.pagination.currentPage = page
      this.getAll()
    },

    // 更改分页大小
    sizeChange(val) {
      this.pagination.pageSize = val
      this.getAll()
    },

    // 添加小说
    addBook() {
      this.isEdit = false
      this.formData = {title: '', author: '', type: '', description: ''}
      this.dialogVisible = true
    },

    // 修改小说
    updateBook(row) {
      this.isEdit = true
      this.formData = {...row}
      this.dialogVisible = true
    },

    // 删除小说
    async deleteBook(row) {
      try {
        await this.$confirm('此操作将永久删除该小说, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })

        try {
          await this.axios.delete(`api/user/books/${row.id}`)
          this.$message.success('删除成功')
          // 优化：直接过滤当前条目代替重新获取
          this.dataList = this.dataList.filter(item => item.id !== row.id)
        } catch (error) {
          if (error.response?.status === 404) {
            this.$message.error('小说不存在')
          } else {
            this.$message.error('删除失败')
          }
        }
      } catch (cancel) {
        // 用户取消删除
      }
    },

    // 提交添加/修改
    submitForm() {
      this.$refs.dataForm.validate(async (valid) => {
        if (valid) {
          try {
            this.loading = true // 添加加载状态

            const url = this.formData.id ? `api/user/books/${this.formData.id}` : 'api/user/books'
            const method = this.formData.id ? 'put' : 'post'

            await this.axios[method](url, this.formData)
            this.$message.success(this.formData.id ? '更新成功' : '添加成功')
            this.dialogVisible = false
            await this.getAll()
          } catch (error) {
            this.loading = false
            console.error('提交失败:', error)
          }
        }
      })
    },

    // 取消提交
    handleCancel() {
      this.dialogVisible = false
      this.$nextTick(() => {
        this.$refs.dataForm.resetFields()
        this.formData = { id: null, title: '', author: '', type: '', description: '' }
      })
    },

    // 重置筛选
    resetInput() {
      this.filters = { title: '', author: '', type: '' }
      this.getAll()
    }
  }
}
</script>

<style scoped>
/* 添加自定义样式 */
.filter-container {
  margin-bottom: 20px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}
</style>