<template>
  <div class="login-container">
    <el-card class="login-card">
      <h2 class="login-title">用户登录</h2>
      <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-position="top"
          class="login-form">

        <el-form-item label="账号" prop="uname">
          <el-input
              v-model="ruleForm.uname"
              prefix-icon="el-icon-user"
              placeholder="请输入账号">
          </el-input>
        </el-form-item>

        <el-form-item label="密码" prop="password">
          <el-input
              type="password"
              v-model="ruleForm.password"
              prefix-icon="el-icon-lock"
              autocomplete="off"
              placeholder="请输入密码"
              show-password>
          </el-input>
        </el-form-item>
      </el-form>

      <div class="btn-group">
        <el-button
            type="primary"
            @click="submitForm('ruleForm')"
            class="login-btn"
            :loading="loading">
          <span v-if="!loading">立即登录</span>
          <i v-else class="el-icon-loading"></i>
        </el-button>

        <div class="action-links">
          <el-link type="primary" @click="resetForm('ruleForm')">忘记密码？</el-link>
          <router-link to="/register">
            <el-link type="primary">立即注册</el-link>
          </router-link>
        </div>
      </div>
    </el-card>

    <div class="footer-text">
      <span>© 2025 版权所有 · 保留所有权利</span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        uname: '',
        password: '',
      },
      // 用户名，密码的约束
      rules: {
        uname: [
          {required:true, message:"用户名不能为空！", trigger:'blur' },
        ],
        password: [
          {required:true, message:"密码不能为空！", trigger:'blur'},
        ],
      },
      loading: false,    // 是否显示加载动画
    };
  },

  methods: {
    submitForm(formName) {
      // 验证表单中的账号密码是否有效，因为 required：true
      this.$refs[formName].validate(valid => {
        // 点击登录后，登录按钮显示加载动画
        this.loading = true;

        // 校验账号密码成功，发送请求到后端登录接口
        if (valid) {
          let _this = this;
          // 使axios将登录信息发送到后端
          this.axios({
            url: '/api/user/login',      // 请求地址
            method: 'post',              // 请求方法
            headers: {                   // 请求头
              "Content-Type": "application/json",
            },
            params: {
              uname: _this.ruleForm.uname,
              password: _this.ruleForm.password,
            },
          }).then((res) => {
            // 当收到后端的响应时执行该括号内的代码，res 为响应信息，也就是后端返回的信息
            if (res.data.code === "0") {
              // 当响应的编码为 0 时，说明登录成功
              // 将用户信息储存到sessionStorage中
              sessionStorage.setItem("userInfo", JSON.stringify(res.data.data));
              // 跳转页面到首页
              this.$router.push('/Home');
              // 显示后端响应的成功信息
              this.$message({
                message: res.data.msg,
                type: 'success',
              });
            } else { // 当响应编码不为 “0”，说明登录失败
              // 显示后端的失败消息
              this.$message({
                message: res.data.msg,
                type: 'warning',
              });
            }
            // 不管成功还是失败，关闭登录按钮的加载动画
            _this.loading = false;
            console.log(res);
          });
        } else { // 账号或密码有一个没填，不发送请求，直接提示
            console.log("error submit!!");
            this.loading = false;
            return false;
          }
        });
      },
          resetForm(formName){
        this.$refs[formName].resetFields();
      },
    },
  };
</script>

<style scoped>
.login-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

.login-card {
  width: 420px;
  padding: 35px;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  transition: transform 0.3s ease;
}

.login-card:hover {
  transform: translateY(-5px);
}

.login-title {
  text-align: center;
  color: #2d3436;
  margin-bottom: 30px;
  font-size: 28px;
  font-weight: 600;
}

.login-form {
  margin-bottom: 25px;
}

.login-form .el-form-item {
  margin-bottom: 22px;
}

.login-form .el-input__prefix {
  left: 10px;
  transition: all 0.3s ease;
}

/* 新增图标控制样式 */
.el-input__inner:focus + .el-input__prefix,
.el-input__inner:not(:placeholder-shown) + .el-input__prefix {
  opacity: 0;
  transform: translateX(-10px);
}

.login-form .el-input__inner {
  padding-left: 35px;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
  transition: all 0.3s ease;
}

.login-form .el-input__inner:focus {
  border-color: #409eff;
  box-shadow: 0 0 8px rgba(64, 158, 255, 0.3);
}

.login-btn {
  width: 100%;
  height: 48px;
  font-size: 16px;
  border-radius: 8px;
  background: linear-gradient(45deg, #409eff, #7367f0);
  border: none;
  transition: all 0.3s ease;
}

.login-btn:hover {
  opacity: 0.9;
  transform: translateY(-2px);
}

.action-links {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
}

.footer-text {
  margin-top: 30px;
  color: #666;
  font-size: 14px;
}

@media (max-width: 480px) {
  .login-card {
    width: 90%;
    padding: 25px;
  }

  .login-title {
    font-size: 24px;
  }
}
</style>